import React from "react"
import { graphql } from 'gatsby'

import Layout from "/src/layouts/main/layout/layout.js"
import Seo from "../components/seo"
import CheatsheetList from '../components/CheatsheetList/CheatsheetList'


const CheatsheetsPage = ({ data }) => {
  
  return (
    <Layout>
      <Seo title="Cheatsheets" />
      {<CheatsheetList cheatsheets={ data.allMarkdownRemark.nodes } />}
    </Layout>
  )
}

export default CheatsheetsPage

export const pageQuery = graphql`query CheatsheetsQuery  {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 1000
      filter: {fields: {collection: {eq: "cheatsheets"}}}
    ) {
      nodes {
        id
        fields {
          slug
        }
        frontmatter {
            title
            icon {
                childImageSharp {
                    gatsbyImageData(
                        layout: CONSTRAINED
                        transformOptions: {}
                        width: 400
                      )
                  }
            }
          }
      }
    }
  }
`