import React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";

const CheatsheetItem = ({ cheatsheet }) => {
    const { title, icon } = cheatsheet.frontmatter
    const { slug } = cheatsheet.fields
    return (
        <Link className="cheatsheets-item__link" to={`/cheatsheets${slug}`}>
            <div className="cheatsheets-item">            
                <div className="cheatsheets-item__img-wrapper">
                    { icon ? <GatsbyImage image={icon.childImageSharp?.gatsbyImageData} alt="" className="cheatsheets-item__img" /> : '' }
                </div>            
                <div className="cheatsheets-item__content">
                    <h3 className="cheatsheets-item__title">
                        {title}
                    </h3>
                </div>            
            </div>
        </Link>
    );

}

export default CheatsheetItem